<template>
  <div class="login">
    <NavBar :toDisplay="{ 'selectionList': true, 'studentLink': true, 'teacherLink': true, 'contactLink': true, 'tosLink': true, 'aboutLink': true }" />
    <div v-if="token">
      <h4 style="color:blue">Already logged in as <span style="color:purple">{{ userType }}</span></h4>
    </div>
    <h1>Login</h1>
    <div>
      <br>
      <br>
      <span>select mock IDM user</span>
      <br>
      <div 
        v-if="mockIdmUsers" 
        class="mock-idm-list"
      >
        <div class="box-inside-mock">
          <h4>מורים</h4>
          <p 
            v-for="teacher in mockIdmUsers.teachers"
            :key="teacher.zehut" 
            class="idmMockBtn"
            @click="getLoginPromise(teacher)"
          >{{ teacher.name }}</p> 
        </div>
        <div class="box-inside-mock">
          <h4>תלמידים</h4>
          <p 
            v-for="student in mockIdmUsers.students"
            :key="student.zehut" 
            class="idmMockBtn"
            @click="getLoginPromise(student)"
          >{{ student.name }}</p> 
        </div>
      </div>
    </div>
    <br>
    <br>
    <button @click="idmLogin">idm login</button>
    <br>
    <br>
    <div>
      <v-dialog />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import NavBar from '@/components/NavBar.vue';
import generalService from '../services/general-service';
import appConfig from '../config/dev-config';
import Notifications from '../services/notifications';
import logger from '@/services/logger.js';

export default {
  name: 'Login',
  components: {
    NavBar
  },
  data() {
    return {
      userData: this.$store.getters.getState,
      mockUserId: null,
      popUpErrorMsg: false,
      token: this.$store.getters.getToken,
      userType: null,
      mockIdmUsers: null
    };
  },
  computed: {
    getToken() {
      return this.$store.getters.getToken;
    },
    getUserType: function() {
      let isTeacher = this.$store.getters.isTeacher;
      if (isTeacher) {
        return 'Teacher';
      } else {
        return 'Student';
      }
    }
  },
  created() {
    this.getMockIdmUsers();
  },
  mounted() {
    if (this.isDevMode()) {
      // Dev Mode
      logger.debug('DEV MODE');

      if (this.isAutoLoginMode()) {
        // Automatic login will be executed now
      }
    } else {
      // Prod Mode
      logger.debug('PROD MODE');
    }

    this.userType = this.getUserType;
  },
  methods: {
    async idmLogin() {
      let result = await generalService.startOpenIdSession();
      if (result.success) {
        window.location.href = result.url;
      }
    },
    async getLoginPromise(userData) {
      let data = await generalService.performMockLogin(userData);
      logger.debug(data);
      if (data.success) {
        if (data.token) {
          // Token returned
          this.$store.dispatch('setLoginData', data);
          if (data.userType === 'T') {
            if (data.showTos && !appConfig.skipTos) {
              this.$router.push('/tos');
            } else {
              this.$router.push('/Teacher');
            }
          } else if (data.userType === 'S') {
            this.$router.push('/Student');
          }
        } else {
          // No token back from server
          this.$store.dispatch('cleanState');
        }
      } else {
        alert(data.errorCode);
      }
    },
    getState() {
      return this.$store.getters.getState;
    },
    isDevMode() {
      if (appConfig.currentMode === appConfig.options.devMode) {
        return true;
      } else {
        return false;
      }
    },
    isAutoLoginMode() {
      if (appConfig.automaticLogin) {
        return true;
      } else {
        return false;
      }
    },
    getMockIdmUsers: async function() {
      let response = await generalService.getMockIdmJSON();
      if (response.success) {
        this.mockIdmUsers = response.idmMockUsers;
        logger.debug(response);
      } else {
        logger.error('Error on getMockIdmUsers request.');
      }
    }
  }
};
</script>

<style>
.mock-idm-list {
  display: inline-flex;
}

.box-inside-mock {
  margin: 10px;
  padding: 10px;
  text-align: right;
}

.idmMockBtn {
  margin: 10px;
  background-color: rgb(206, 206, 206);
  border-radius: 15px;
  padding: 10px;
  color: black;
  cursor: pointer;
  text-align: center;
  transition: 0.3s;
}

.idmMockBtn:hover {
  transition: 0.3s;
  background-color: rgb(41, 46, 75);
  color: white;
  box-shadow: 0px 0px 10px -1px black;
}
</style>
