<template>
  <div>
    <NavBar :toDisplay="{ 'selectionList': true, 'studentLink': true, 'teacherLink': true, 'contactLink': true, 'tosLink': true, 'aboutLink': true }" />
    <div class="loader" />
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue';
import generalService from '../services/general-service';
import appConfig from '../config/dev-config';
import logger from '@/services/logger.js';

export default {
  name: 'IdmCallback',
  components: {
    NavBar
  },
  created() {
    this.processIdmCallback();
  },
  methods: {
    async processIdmCallback() {
      try {
        let loginResult = await generalService.openIdLogin(
          window.location.href
        );
        if (loginResult.success) {
          let data = loginResult;
          // Token returned
          this.$store.dispatch('setLoginData', data);
          if (data.userType === 'T') {
            if (data.showTos && !appConfig.skipTos) {
              this.$router.push('/tos');
            } else {
              this.$router.push('/Teacher');
            }
          } else if (data.userType === 'S') {
            this.$router.push('/Student');
          }
          logger.debug(loginResult);
        } else {
          // No token back from server
          this.$store.dispatch('cleanState');
          logger.error('Error logging in: ' + loginResult.errorCode);
        }
      } catch (err) {
        // TODO: Replace this with some notify message
        console.error(
          new Date() + ': Error processing login/get-lessons: ' + err
        );
      }
    }
  }
};
</script>

<style>
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
